import uniNavBar from "@/components/uni-nav-bar/uni-nav-bar.vue"
import commonAvatar from "@/components/commonAvatar.vue"
export default{
	data(){
		return{
			rightIcon: '/static/ling.png',
			dot: true,
			color: '#333',
			background: '#fff',
			link: 'YdhkSnm',
			code_img: '/static/code.png'
		}
	},
	components:{
		uniNavBar,
		commonAvatar
	},
	methods:{
		copyLink(){
			uni.setClipboardData({
			    data: this.link,
			    success: function () {
			        console.log('success');
			    }
			});
			uni.getClipboardData({
			    success: function (res) {
			        console.log(res.data);
			    }
			});
		},
		savaCode(){
			let that = this;
			uni.showLoading({
				title: '保存中'
			})
			uni.downloadFile({
				url: that.code_img,
				success: (ress) => {
					if (ress.statusCode === 200) {
						console.log(ress.tempFilePath);
						uni.saveImageToPhotosAlbum({
							filePath: ress.tempFilePath,
							success: function () { 
								uni.hideLoading();
								uni.showToast({
									title: '下载成功',
									icon: 'none',
									duration: 1500
								})
							},
						})
					}
				},
				fail() {
					uni.hideLoading();
					uni.showToast({
						title: '下载失败！',
						icon: 'none',
						duration: 1500
					})
				}
			});
		}
	}
}
